  font-weight: 400; }
  p.alt {
    font: normal 14px/1.5 'Space Mono', monospace; }

span.Button {
  font: normal 16px/1.5 'Space Mono', monospace;
  font-weight: 700; }

.row {
  width: 100%; }

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto; }

.center {
  text-align: center; }

@-webkit-keyframes blink {
  0% {
    opacity: 0; }
  33%,
  66% {
    opacity: 1; }
  to {
    opacity: .6; } }

@keyframes blink {
  0% {
    opacity: 0; }
  33%,
  66% {
    opacity: 1; }
  to {
    opacity: .6; } }

@-webkit-keyframes floatUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes floatUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes colFadeIn {
  0% {
    background-color: transparent; }
  100% {
    background-color: #fff; } }

@keyframes colFadeIn {
  0% {
    background-color: transparent; }
  100% {
    background-color: #fff; } }

@-webkit-keyframes portfolioShow {
  0% {
    left: 0;
    right: 100vw; }
  50% {
    left: 0;
    right: 0; }
  100% {
    left: 100vw;
    right: 0; } }

@keyframes portfolioShow {
  0% {
    left: 0;
    right: 100vw; }
  50% {
    left: 0;
    right: 0; }
  100% {
    left: 100vw;
    right: 0; } }

.HomePage {
  width: 100%; }


